.nav-item {
  display: block;
  position: relative; }

.nav-item a:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0%;
  height: 3px;
  background: red;
  opacity: 0;
  border-radius: 10px;
  transition: left 0.7s, width 0.7s, opacity 1s; }

.nav-item a:hover:after {
  left: 0;
  width: 100%;
  opacity: 1; }

.slider {
