.custom-dots {
  position: absolute;
  /* абсолютное позиционирование , в данном случае потому что я работаю в пустом проекте */
  right: 5%;
  top: 50%; }

.custom-dots ul {
  list-style: none;
  /*  убираем точки от тега ul */
  display: flex;
  flex-direction: column; }

.custom-dots ul li button {
  /* кастомизируем наши точки */
  background: red;
  width: 15px;
  height: 15px;
  border: none; }

.slick-dots li button::before {
  opacity: 0 !important;
  /* убираем старые */ }

